import React from "react";
import { BrowserRouter, Route, Link, Switch } from "react-router-dom";

import Main from "./Main";
import Page2 from "./Page2";

export default () => (
  <BrowserRouter>
    <Switch>
      <Route exact path="/" component={Main} />
      <Route exact path="/page2" component={Page2} />
    </Switch>
  </BrowserRouter>
);



